<template>
  <div class="erg-right-menu">
    <base-erg ref="erg" />
    <parent-association
      v-if="associationVisible"
      id="11"
      v-model:dialogVisible="associationVisible"
    ></parent-association>
  </div>
</template>

<script lang="ts" setup>
import { ref, onMounted, computed } from 'vue'
import BaseErg from './Base.vue'
import { rightMenu, initRightMenu } from './assets/js/right-menu'
import ParentAssociation from './components/parent-association.vue'

const erg = ref()
const associationVisible = ref(false)

const graph = computed(() => erg.value.graph)
const useRightMenu = () => {
  graph.value.updateConfig({
    useRightMenu: true,
  })

  graph.value.on('contextmenu:show', (info) => {
    console.log(info.target)
    graph.value.updateConfig({
      rightMenu: info.target ? rightMenu : initRightMenu,
    })
  })
  graph.value.on('contextmenu:click', (info) => {
    console.log(info.target)
    switch (info.value) {
      case 'level-1':
        associationVisible.value = true
        break
      case 'level-2':
        associationVisible.value = true
        break
      case 'level-all':
        associationVisible.value = true
        break

      default:
        break
    }
  })
}

onMounted(useRightMenu)

</script>

<style lang="postcss">
.erg-right-menu {
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>
