<template>
  <div class="erg-extra-drawing">
    <base-erg ref="erg" />
  </div>
</template>

<script lang="ts" setup>
import { ref, onMounted, computed } from 'vue'
import BaseErg from './Base.vue'
import { defaultData as data } from './assets/js/mock'

const erg = ref()

const graph = computed(() => erg.value.graph)
const useExtraDrawing = () => {
  graph.value.on('graph:beforeUpdate', (ctx) => {
    data.nodeDataArray.map(({id}, idx) => {
      if (idx % 2) return

      const node = graph.value.dataHandler.originDataMap.get(id)
      if (!node) return
      ctx.save()
      // 设置字体
      ctx.font = '16px'
      // 设置颜色
      ctx.fillStyle = '#F59A23'
      ctx.strokeStyle = '#F59A23'
      // 设置水平对齐方式
      ctx.textAlign = 'right'
      // 设置垂直对齐方式
      ctx.textBaseline = 'top'
      ctx.fillText('正在编辑', node.x + node.width, node.y - 22)
      ctx.lineWidth = 2
      ctx.strokeRect(node.x, node.y, node.width, node.height)
      ctx.restore()
    })
  })
}

onMounted(useExtraDrawing)
</script>

<style lang="postcss">
.erg-extra-drawing {
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>
