      </el-button>
      <el-button v-else size="small" circle title="编辑" @click="setEditable">
        <template #icon>
          <svg>
            <use :xlink:href="`${Icons}#unlock`"></use>
          </svg>
        </template>
      </el-button>
      <el-button size="small" circle title="批量创建连线" @click="createLines">
        <template #icon>
