<script lang="ts" setup>
import { reactive, onBeforeUnmount, onMounted, ref } from 'vue'
import { ElButton, ElDivider, ElColorPicker, ElSelect, ElOption } from 'element-plus'
import { Erg as Graph, ErgViewType } from '@ej/erg-renderer/src'
import { defaultData as data } from './assets/js/mock'
import Icons from './assets/icon/symbol-defs.svg'

const props = withDefaults(
  defineProps<{
    useThumbnail: boolean
    useRightMenu: boolean
    useAttrFocus: boolean
    editable: string
  }>(),
  {
    useThumbnail: true,
    useRightMenu: true,
    useAttrFocus: true,
    editable: 'edit',
  },
)

// 渲染器实例无需设为代理数据，这里只是方便Expose使用！
const graph = ref()

const state = reactive({
  scale: 100,
  bgC: '',
  bdC: '',
  color: '',
  lock: 'edit',
  viewType: ErgViewType.Detail,
})

const mountGraph = () => {
  const fontFamily = '-apple-system, Noto Sans, Helvetica Neue, Helvetica, Nimbus Sans L, Arial, Liberation Sans, PingFang SC, Hiragino Sans GB, Noto Sans CJK SC, Source Han Sans SC, Source Han Sans CN, Microsoft YaHei, Wenquanyi Micro Hei, WenQuanYi Zen Hei, ST Heiti, SimHei, WenQuanYi Zen Hei Sharp, sans-serif'
  graph.value = new Graph({
    el: document.getElementById('canvas'),
    fontFamily,
    editable: state.lock,
    useThumbnail: props.useThumbnail,
    useAttrFocus: props.useAttrFocus,
    externalFn: {
      addNode: function (obj) {
        const str = prompt('请输入表名：')
        if (!str) return
        obj.name = str
        data.nodeDataArray.push(obj)
      },
      delNode: function (key) {
        console.log(key)
      },
      addLine: function (obj) {
        console.log('添加线了')
        obj.numLineTypes = 2
        data.linkDataArray.push(obj)
      },
      delLine: function (key) {
        console.log(key)
      },
      addFilter: function (obj) {
        obj.filter && data.nodeDataArray.push(obj.filter)
        obj.toFilter && data.linkDataArray.push(obj.toFilter)
        data.linkDataArray.push(obj.fromFilter)
      },
      delFilter: function (key) {
        console.log(key)
      },
      addMark: function (obj) {
        data.markDataArray.push(obj)
        markMap.set(obj.id, obj)
      },
      delMark: function (key) {
        console.log(key)
      },
      addNote: function (obj) {
        const str = prompt('请输入备注内容：')
        if (!str) return
        obj.text = str
        data.noteDataArray.push(obj)
      },
      delNote: function (key) {
        console.log(key)
      },
      $message: function (val) {
        console.log(val.message)
      },
      handleCopy(ev1, ev2) {
        console.log(ev1, ev2)
        return true
      },
      handlePaste(nodes) {
        data.nodeDataArray.push(...nodes)
      },
    }
  })
  graph.value.update(data)
  // graph.value.on('graph:zoom', (info) => console.log('graph:zoom', info))
  // graph.value.on('graph:move', (info) => console.log('graph:move', info))
  // graph.value.on('graph:beforeUpdate', (ctx) => null)
  // graph.value.on('graph:afterUpdate', (ctx) => null)
  // graph.value.on('graph:mousedown', (info) => console.log('graph:mousedown', info))
  // graph.value.on('graph:mouseup', (info) => console.log('graph:mouseup', info))
  // graph.value.on('contextmenu:show', (info) => null)
  // graph.value.on('contextmenu:click', (info) => null)
  // graph.value.on('node:move', (info) => console.log('node:move', info))
  // graph.value.on('node:enter', (info) => console.log('node:enter', info))
  // graph.value.on('node:leave', (info) => console.log('node:leave', info))
  // graph.value.on('node:mousedown', (info) => console.log('node:mousedown', info))
  // graph.value.on('node:mouseup', (info) => console.log('node:mouseup', info))
  // graph.value.on('line:enter', (info) => console.log('line:enter', info))
  // graph.value.on('line:leave', (info) => console.log('line:leave', info))
  // graph.value.on('line:mousedown', (info) => console.log('line:mousedown', info))
  // graph.value.on('line:mouseup', (info) => console.log('line:mouseup', info))
  // graph.value.on('mark:enter', (info) => console.log('mark:enter', info))
  // graph.value.on('mark:leave', (info) => console.log('mark:leave', info))
  // graph.value.on('mark:mousedown', (info) => console.log('mark:mousedown', info))
  // graph.value.on('mark:mouseup', (info) => console.log('mark:mouseup', info))
  // graph.value.on('note:enter', (info) => console.log('note:enter', info))
  // graph.value.on('note:leave', (info) => console.log('note:leave', info))
  // graph.value.on('note:mousedown', (info) => console.log('note:mousedown', info))
  // graph.value.on('note:mouseup', (info) => console.log('note:mouseup', info))
}
const unmountGraph = () => {
  graph.value.destroy()
}
const handleFullscreen = () => {
  graph.value.fullscreen()
}

const changeCanvasScale = (scaleRatio) => {
  if (state.scale > 20) state.scale += scaleRatio
  graph.value.zoom(state.scale)
}

const addNode = () => {
  graph.value.addNode({
    id: graph.value.createKey(),
    attributes: [],
  })
}

const addLine = (lineType) => {
  graph.value.addLine(lineType)
}

const addMark = () => {
  graph.value.addMark()
}

const addNote = () => {
  graph.value.addNote()
}

const changeColor = (value, tp) => {
  graph.value.changeColor(value, tp)
}

const setEditable = () => {
  state.lock = state.lock === 1 ? 2 : 1
  graph.value.setEditable(state.lock)
}

const copy = () => {
  graph.value.copy()
}

const createLines = () => {
  const list = [
    {
      from: 0,
      to: 1,
      pos: [
        { x: 200, y: 125 },
        { x: 400, y: 125 },
      ],
      type: 'primaryKey',
      numLineTypes: 1,
    },
  ]
  graph.value.createLines(list)
}

const updateLayout = () => graph.value.updateLayout()

onMounted(mountGraph)
onBeforeUnmount(unmountGraph)

defineExpose({
  graph,
})
</script>

